import ScreenshotToolEvent from './screenshot-tool-event'

export default class ScreenshotTool {
  /**
   * 
   * @type {string}
   */
  #name = ''
  /**
   * 
   * @type {string}
   */
  #color = 'white'

  /**
   * 
   * @type {boolean}
   */
  #disabled = false
  /**
   * 
   * @type {boolean}
   */
  #active = false

  /**
   * 
   * @type ScreenshotToolEvent
   */
  #events
  /**
   * 
   * @type Function
   */
  #activeEvent
  /**
   * 
   * @type Function
   */
  #pauseEvent

  /**
   * 
   * @type HTMLElement
   */
  #node
  /**
   * 
   * @type HTMLElement
   */
  #iconNode

  /**
   * Screenshot constructor
   * @param name {string} 
   * @param iconClass {string} 
   * @param [color='white'] {string} 
   * @param [disabled] {boolean} 
   * @param [clickEvent] {function} 
   * @param [activeEvent] {function} 
   * @param [pauseEvent] {function} 
   */
  constructor ({ name, iconClass, color, disabled, clickEvent, activeEvent, pauseEvent } = {}) {
    this.#node = document.createElement('div')
    this.#node.classList.add('screenshot-toolbar-tool')
    this.#initEvents()

    name && (this.name = name)
    color && (this.color = color)
    disabled && (this.disabled = disabled)
    iconClass && (this.iconClass = iconClass)
    activeEvent && (this.#activeEvent = activeEvent)
    pauseEvent && (this.#pauseEvent = pauseEvent)
    if (clickEvent) {
      this.events.add('click', clickEvent)
    }
  }

  get node () {
    return this.#node
  }

  get events () {
    return this.#events
  }

  get name () {
    return this.#name
  }

  set name (val) {
    this.#name = val
    this.node?.setAttribute('title', val)
  }

  get disabled () {
    return this.#disabled
  }

  set disabled (val) {
    this.#disabled = !!val
    if (val) {
      this.active = false
      this.node?.classList.add('disabled')
    } else {
      this.node?.classList.remove('disabled')
    }
  }

  get active () {
    return this.#active
  }

  set active (val) {
    if (this.active === !!val) {
      return
    }
    this.#active = !!val
    if (val) {
      this.disabled = false
      this.node?.classList.add('active')
      if (this.#activeEvent) {
        this.#activeEvent()
      }
    } else {
      this.node?.classList.remove('active')
      if (this.#pauseEvent) {
        this.#pauseEvent()
      }
    }
  }

  get color () {
    return this.#color
  }

  set color (val) {
    this.#color = val
    if (this.node) {
      this.node.style.color = val
    }
  }

  get iconClass () {
    return this.#iconNode.classList
  }

  set iconClass (val) {
    if (this.#iconNode) {
      this.#iconNode.remove()
    }
    if (this.node && val && val.trim()) {
      this.#iconNode = document.createElement('span')
      this.#iconNode.classList.add('icon', val.trim())
      this.node.append(this.#iconNode)
    }
  }

  #initEvents () {
    this.#events = new ScreenshotToolEvent(this.node)
  }
}
